import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, AngularFireObject } from '@angular/fire/database';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-secret-message',
  templateUrl: './secret-message.component.html',
  styleUrls: ['./secret-message.component.scss']
})
export class SecretMessageComponent implements OnInit {

  regName: string;
  regPass: string;
  prevName: string;
  currentName: string;
  password: string;
  showReg: boolean;
  registred: boolean;
  passValid: boolean;
  already_used = false;
  empty_msg = false;
  data;
  used_names: string[];
  dbObject: AngularFireObject<any>;
  router: Router;
  route: ActivatedRoute;

  constructor(db: AngularFireDatabase, router: Router, route: ActivatedRoute) {
    this.dbObject = db.object('/people');
    this.dbObject.valueChanges()
      .subscribe(data => {
        this.data = data
        this.used_names = Object.getOwnPropertyNames(data);
        let used = false;
        for (let i = 0; i < this.used_names.length; i++) {
          if(this.currentName == this.used_names[i]){
            used = true;
            break;
          }
        }
        if(this.currentName == null && this.regName != '') used = true;
        if(!used){
          this.showReg = true;
          this.registred = false;
          this.passValid = false;
        }
      });
    this.router = router;
    this.route = route;
    this.prevName = '';
    this.passValid = false;
  }

  ngOnInit(): void {
    this.registred = false;
    this.route.paramMap
      .subscribe((params: ParamMap) => {
        this.currentName = params.get('name');
        if (this.currentName == null) {
          this.showReg = true;
          this.registred = false;
        }
        else {
          if (this.currentName != this.prevName) {
            this.password = '';
            this.passValid = false;
          }
          this.showReg = false;
          this.registred = true;
          this.prevName = this.currentName;
        }
      })
  }

  submitData() {
    console.log("submit routine", this.regName, this.regPass);
    let inputField = document.getElementById('userRegNameInput');
    for (let i = 0; i < this.used_names.length; i++) {
      if (this.regName == this.used_names[i]) {
        inputField.classList.add('invalid')
        this.already_used = true;
        return;
      }
    }
    inputField.classList.remove('invalid');
    let new_entry = Object();
    new_entry[this.regName] = Object();
    new_entry[this.regName]['password'] = this.regPass;
    new_entry[this.regName]['messages'] = [];
    console.log(new_entry);
    this.dbObject.update(new_entry);
    this.registred = true;
  }

  gotoLink() {
    this.router.navigate(['.', { name: this.regName }]);
  }

  submitPassword() {
    if (this.data[this.currentName].password == this.password) {
      this.passValid = true;
    }
  }

  addMessage(){
    let message :any = document.getElementById('MessageAdd');
    if(message.value == ""){
      this.empty_msg = true;
      return;
    }
    this.empty_msg = false;
    if(this.data[this.currentName]['messages'] == undefined){
      this.data[this.currentName]['messages'] = [message.value];
    }
    else{
      console.log(this.data[this.currentName]['messages']);
      this.data[this.currentName]['messages'].push(message.value);
    }
    this.dbObject.update(this.data);
    message.value = "";
  }
}
